.l-pink {
  background-color: #FFF5F4;
}

.d-pink {
  background-color: #E6BDBB;
}

.logo {
  filter: brightness(0.5) contrast(200%);
}

.bk-container {
  background-repeat: repeat;
  background-attachment: scroll;
  background-position: center top;
  color:#52201f;
  min-height: calc( 100vh );
}

.pd {
  color:#be8c9a;
  height:3px;
}

.gray-blue {
  color:#33425f;
}

