.text-area {
  height:150px;
}

.select-feedback {
  width: 100%;
  margin-top: 0.25rem;
  font-size: .875em;
  color: #dc3545;
}

.fix {
  position: absolute;
  top: 0px;
  left: 0px;
}

.haze {
  background-color: #2c3db37a;
  color: white;
  font-size: 4em;
  font-weight: bold;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.banner-container {
  height: 250px;
  overflow: hidden;
}

.banner {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.h-63 {
  height: 63%;
}

.upload-zone {
  width: 100%;
}

.Recaptcha-checkbox-border {
  height: 17px;
  width: 17px;
}